import { useStaticQuery, graphql } from 'gatsby'

const AllPostContentQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___modifiedTime], order: DESC }
					filter: { frontmatter: { type: { eq: "post" }, draft: { ne: true } } }
				) {
					edges {
						node {
							id
							body
							frontmatter {
								title
								description
								thumbnail
								categories
								tags
								keywords
								publishTime
								modifiedTime
							}
							fields {
								slug
								name
							}
						}
					}
				}
			}
		`
	)
	return allMdx.edges
}

export default AllPostContentQuery
