import { useStaticQuery, graphql } from 'gatsby'

const AllHookContentQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___order], order: ASC }
					filter: { frontmatter: { type: { eq: "hook" }, draft: { ne: true } } }
				) {
					edges {
						node {
							id
							body
							frontmatter {
								hookType
								title
								description
								order
							}
							fields {
								slug
								name
							}
						}
					}
				}
			}
		`
	)
	return allMdx.edges
}

export default AllHookContentQuery
