import { useStaticQuery, graphql } from 'gatsby'

const AllTagContentQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___order], order: ASC }
					filter: { frontmatter: { type: { eq: "tag" }, draft: { ne: true } } }
				) {
					edges {
						node {
							id
							body
							frontmatter {
								title
								description
								thumbnail
								order
							}
							fields {
								slug
								name
							}
						}
					}
				}
			}
		`
	)
	return allMdx.edges
}

export default AllTagContentQuery
